.loginPage{
    background-color: var(--white);
    padding: 40px;
    padding-top: 110px;
    padding-bottom: 96px;
    width: 100%;
    height: 100vh;
    overflow-y: scroll;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    
}

.loginCard{
    min-width: 500px;
    padding: 32px 100px;
    background-color: var(--card-color);
    border-radius: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap:16px
}

.loginCardHeader{
    font-size: 32px;
    font-weight: 500;
    color: var(--text-color)
}

.loginInput{
    width: 100%;
    height: 60px;
    border-radius: 8px;
}
.loginButton{
    width: 100%;
    height: 60px;
    border-radius: 8px;

}

.loginCardDescr{
    font-size: 16px;
    font-weight: 300;
}


.loginCardLogo{
    width: 50%;
}